"use client";
import React, { useState, useEffect } from "react";
import {
  Mail,
  Lock,
  User,
  Phone,
  Home,
  Heart,
  Moon,
  Sun,
  Eye,
  EyeOff,
  Drop,
} from "lucide-react";
import { motion } from "framer-motion";

const DonerRegistration = () => {
  const [formData, setFormData] = useState({
    fullName: "",
    email: "",
    password: "",
    confirmPassword: "",
    bloodType: "",
    age: "",
    phone: "",
    address: "",
  });

  const [focusedInput, setFocusedInput] = useState("");
  const [isDarkMode, setIsDarkMode] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [emailError, setEmailError] = useState("");
  const [passwordErrors, setPasswordErrors] = useState({
    length: false,
    number: false,
    uppercase: false,
    lowercase: false,
    special: false,
  });
  const [formError, setFormError] = useState("");

  const containerVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.6,
        when: "beforeChildren",
        staggerChildren: 0.2,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, x: -20 },
    visible: {
      opacity: 1,
      x: 0,
      transition: { duration: 0.5 },
    },
  };

  const validateEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!email) {
      setEmailError("Email is required");
    } else if (!emailRegex.test(email)) {
      setEmailError("Please enter a valid email address");
    } else {
      setEmailError("");
    }
  };

  const validatePassword = (password) => {
    setPasswordErrors({
      length: password.length >= 8 && password.length <= 12,
      number: /\d/.test(password),
      uppercase: /[A-Z]/.test(password),
      lowercase: /[a-z]/.test(password),
      special: /[!@#$%^&*(),.?":{}|<>]/.test(password),
    });
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });

    if (name === "email") validateEmail(value);
    if (name === "password") validatePassword(value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    validateEmail(formData.email);
    validatePassword(formData.password);

    const isPasswordValid = Object.values(passwordErrors).every(Boolean);
    if (
      !emailError &&
      isPasswordValid &&
      formData.password === formData.confirmPassword
    ) {
      console.log("Registered successfully with:", formData);
      setFormError("");
    } else {
      setFormError("Please fix the errors before submitting.");
    }
  };

  const toggleDarkMode = () => {
    setIsDarkMode(!isDarkMode);
  };

  const PasswordCriteria = ({ met, text }) => (
    <div className="flex items-center gap-2">
      <div
        className={`w-2 h-2 rounded-full ${
          met ? "bg-green-500" : "bg-red-500"
        }`}
      />
      <span
        className={`text-sm ${isDarkMode ? "text-gray-300" : "text-gray-600"}`}
      >
        {text}
      </span>
    </div>
  );

  return (
    <motion.div
      initial="hidden"
      animate="visible"
      variants={containerVariants}
      className={`h-screen w-screen ${
        isDarkMode
          ? "bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900"
          : "bg-gradient-to-br from-red-50 via-white to-red-50"
      } flex flex-col justify-center py-12 sm:px-6 lg:px-8`}
    >
      <motion.button
        onClick={toggleDarkMode}
        className="fixed top-4 right-4 p-2 rounded-full bg-white/80 backdrop-blur-sm shadow-lg hover:bg-gray-100 transition-colors duration-200"
        whileHover={{ scale: 1.1 }}
        whileTap={{ scale: 0.9 }}
      >
        {isDarkMode ? (
          <Sun className="h-6 w-6 text-yellow-500" />
        ) : (
          <Moon className="h-6 w-6 text-gray-800" />
        )}
      </motion.button>

      <motion.div
        className="sm:mx-auto sm:w-full sm:max-w-md"
        variants={itemVariants}
      >
        <motion.div
          className="flex justify-center"
          whileHover={{ scale: 1.1, rotate: 360 }}
          whileTap={{ scale: 0.9 }}
          transition={{ duration: 0.5 }}
        >
          <Heart
            className={`w-16 h-16 ${
              isDarkMode ? "text-red-600" : "text-red-500"
            } drop-shadow-lg`}
          />
        </motion.div>
        <motion.h2
          variants={itemVariants}
          className={`mt-6 text-center text-4xl font-bold ${
            isDarkMode ? "text-white" : "text-gray-900"
          } drop-shadow-sm`}
        >
          Register as a Donor
        </motion.h2>
        <motion.p
          variants={itemVariants}
          className={`mt-2 text-center text-lg ${
            isDarkMode ? "text-gray-300" : "text-gray-600"
          }`}
        >
          Join us and save lives
        </motion.p>
      </motion.div>

      <motion.div
        variants={containerVariants}
        className="mt-8 sm:mx-auto sm:w-full sm:max-w-md"
      >
        <motion.div
          variants={itemVariants}
          className={`${
            isDarkMode ? "bg-gray-800/80" : "bg-white/80"
          } backdrop-blur-sm py-8 px-4 shadow-2xl sm:rounded-2xl sm:px-10`}
        >
          <form className="space-y-6" onSubmit={handleSubmit}>
            {/* Full Name */}
            <motion.div variants={itemVariants}>
              <label
                htmlFor="fullName"
                className={`block text-sm font-medium ${
                  isDarkMode ? "text-gray-300" : "text-gray-700"
                }`}
              >
                Full Name
              </label>
              <motion.div className="mt-1">
                <input
                  id="fullName"
                  name="fullName"
                  type="text"
                  value={formData.fullName}
                  onChange={handleChange}
                  className={`block w-full py-3 pl-3 pr-3 rounded-lg focus:outline-none transition-all duration-200 ${
                    isDarkMode
                      ? "bg-gray-700 text-white"
                      : "bg-white text-gray-900"
                  }`}
                  placeholder="Enter your full name"
                />
              </motion.div>
            </motion.div>

            {/* Email */}
            <motion.div variants={itemVariants}>
              <label
                htmlFor="email"
                className={`block text-sm font-medium ${
                  isDarkMode ? "text-gray-300" : "text-gray-700"
                }`}
              >
                Email address
              </label>
              <motion.div className="mt-1 relative rounded-lg">
                <input
                  id="email"
                  name="email"
                  type="email"
                  value={formData.email}
                  onChange={handleChange}
                  className={`block w-full py-3 pl-3 pr-3 rounded-lg focus:outline-none transition-all duration-200 ${
                    isDarkMode
                      ? "bg-gray-700 text-white"
                      : "bg-white text-gray-900"
                  }`}
                  placeholder="Enter your email"
                />
              </motion.div>
              {emailError && (
                <p className="text-sm text-red-500">{emailError}</p>
              )}
            </motion.div>

            {/* Password */}
            <motion.div variants={itemVariants}>
              <label
                htmlFor="password"
                className={`block text-sm font-medium ${
                  isDarkMode ? "text-gray-300" : "text-gray-700"
                }`}
              >
                Password
              </label>
              <motion.div className="mt-1 relative rounded-lg">
                <input
                  id="password"
                  name="password"
                  type={showPassword ? "text" : "password"}
                  value={formData.password}
                  onChange={handleChange}
                  className={`block w-full py-3 pl-3 pr-3 rounded-lg focus:outline-none transition-all duration-200 ${
                    isDarkMode
                      ? "bg-gray-700 text-white"
                      : "bg-white text-gray-900"
                  }`}
                  placeholder="Enter your password"
                />
              </motion.div>
              <PasswordCriteria /> {/* Display password criteria */}
            </motion.div>
          </form>
        </motion.div>
      </motion.div>
    </motion.div>
  );
};
export default DonerRegistration;
